.wrapper{
margin-left: var(--spacing-large);
padding: var(--spacing-medium) 0;
}

.list__container{
margin: 0;
padding: 0;
list-style-type: none;
}

.list__item{
display: flex;
align-items: center;
margin-bottom: var(--margin-list);
}

.item__episode{
padding: var(--spacing-ultra-small);
border-radius: var(--border-radius-small);
background-color: var(--color-red);
}

.colon{
    width: 20px;
    font-weight: var(--font-bold);
    text-align: center;
    padding: 0 var(--spacing-ultra-small);
}

.title{
text-shadow: var(--text-shadow-yellow);
}

@media screen and (max-width:1020px) {
    .wrapper{
        margin: 0;
        width: 100%;
        }
}

@media screen and (max-width: 860px) and (orientation: landscape) {
    .wrapper{
        margin: 0;
        padding: 0;
        width: auto;
        }
       
        .list__container{
            width: 250px;
        }
        .item__episode, .title{
            font-size: 14px;
            font-weight: normal;
        }
       
         .list__item{
            display: inline-flex;
            flex: 1;
        }
        .item__episode{
            min-width: 80px;
        }
}